import { CartTaxItem } from '@/checkout/__generated__/types';
import { OrderSummarySkeleton } from '@/checkout/components';
import {
  OrderSummary as OrderSummaryComponent,
  Summary,
} from '@/checkout/components/OrderSummary/OrderSummary';
import { useStore } from '@/checkout/context/store';
import { cartSignal, selectedShippingMethodSignal } from '@/checkout/signals';
import { Container } from '@adobe/elsie/lib';
import { HTMLAttributes } from 'preact/compat';
import { ZERO_AMOUNT } from './constants';

export interface OrderSummaryProps extends HTMLAttributes<HTMLDivElement> {}

export type OrderSummaryState = {
  isLoading: boolean;
  summary?: Summary;
};

const INCLUDE_TAX = 2;

const loadingState: OrderSummaryState = {
  isLoading: true,
};

export function useOrderSummary(): OrderSummaryState {
  const { config } = useStore();
  const isLoadingStoreConfig = config === undefined;

  const cartData = cartSignal.value.data;
  const isLoadingCartData = cartData === undefined;

  if (isLoadingStoreConfig || isLoadingCartData) return loadingState;

  const taxCartDisplay = config.taxCartDisplay;
  const inclTaxInSubtotal =
    taxCartDisplay.shoppingCartDisplaySubtotal === INCLUDE_TAX;

  const shippingAddresses = cartData?.shippingAddresses;
  const hasAddresses = !!shippingAddresses?.length;

  const prices = cartData?.prices;

  const grandTotal = prices?.grand_total;
  const hasGrandTotal = !!grandTotal;

  const subtotal = inclTaxInSubtotal
    ? prices?.subtotal_including_tax
    : prices?.subtotal_excluding_tax;

  const appliedTaxes = prices?.applied_taxes || [];
  const hasTaxesApplied = !!appliedTaxes.length;

  if (!hasGrandTotal) return { isLoading: false };

  const summary: Summary = {
    total: {
      inclTax: {
        amount: grandTotal.value!,
        currency: grandTotal.currency!,
      },
    },
    subtotal: {
      amount: subtotal!.value!,
      currency: subtotal!.currency!,
      isTaxIncl: inclTaxInSubtotal,
    },
  };

  if (hasTaxesApplied) {
    const totalTaxAmount = appliedTaxes.reduce(
      (total, tax) => total + tax!.amount.value!,
      ZERO_AMOUNT
    );

    const firstTax = appliedTaxes[0];

    summary.tax = {
      amount: totalTaxAmount,
      currency: firstTax!.amount.currency!,
    };

    if (taxCartDisplay.shoppingCartDisplayFullSummary) {
      summary.tax.breakdown = appliedTaxes as CartTaxItem[];
    }
  }

  if (!hasTaxesApplied && taxCartDisplay.shoppingCartDisplayZeroTax) {
    summary.tax = {
      amount: ZERO_AMOUNT,
      currency: summary.total.inclTax.currency,
    };
  }

  if (taxCartDisplay.shoppingCartDisplayGrandTotal) {
    const totalExclTax = summary.tax
      ? summary.total.inclTax.amount - summary.tax.amount
      : summary.total.inclTax.amount;

    summary.total.exclTax = {
      amount: totalExclTax,
      currency: summary.total.inclTax.currency,
    };
  }

  if (hasAddresses) {
    const firstAddress = shippingAddresses[0];
    const shippingMethod = firstAddress?.selectedShippingMethod;

    if (!shippingMethod) return { isLoading: false, summary };

    summary.shipping = {
      amount: shippingMethod?.amount.value!,
      currency: shippingMethod?.amount.currency!,
      isEstimated: false,
    };

    return { isLoading: false, summary };
  }

  if (!selectedShippingMethodSignal.value) return { isLoading: false, summary };

  summary.shipping = {
    amount: selectedShippingMethodSignal.value.amount.value,
    currency: selectedShippingMethodSignal.value.amount.currency,
    isEstimated: true,
  };

  summary.total.inclTax.amount += summary.shipping.amount;
  if (summary.total.exclTax) {
    summary.total.exclTax.amount += summary.shipping.amount;
  }

  return { isLoading: false, summary };
}

export const OrderSummary: Container<OrderSummaryProps> = ({ ...props }) => {
  const { isLoading, summary } = useOrderSummary();

  return (
    <div {...props}>
      {!summary && <OrderSummarySkeleton />}

      {summary && (
        <OrderSummaryComponent isLoading={isLoading} summary={summary} />
      )}
    </div>
  );
};
