import { FunctionComponent } from 'preact';
import { HTMLAttributes } from 'preact/compat';
import { classes } from '@adobe/elsie/lib';
import '@/checkout/components/OrderSummary/OrderSummary.css';
import { Heading } from '@/checkout/components';
import { Text } from '@adobe/elsie/i18n';
import { Card, Divider, Price, ProgressSpinner } from '@adobe/elsie/components';
import { CartTaxItem } from '@/checkout/__generated__/types';

export type Summary = {
  total: {
    inclTax: {
      amount: number;
      currency: string;
    };
    exclTax?: {
      amount: number;
      currency: string;
    };
  };
  subtotal: {
    amount: number;
    currency: string;
    isTaxIncl: boolean;
  };
  shipping?: {
    amount: number;
    currency: string;
    isEstimated: boolean;
  };
  tax?: {
    amount: number;
    currency: string;
    breakdown?: CartTaxItem[];
  };
};

export interface OrderSummaryProps
  extends Omit<HTMLAttributes<HTMLDivElement>, 'summary'> {
  isLoading?: boolean;
  summary: Summary;
}

export const OrderSummary: FunctionComponent<OrderSummaryProps> = ({
  className,
  summary,
  isLoading = false,
  ...props
}) => {
  const { total, subtotal, shipping, tax } = summary;

  return (
    <Card
      {...props}
      className={classes([
        'checkout-order-summary',
        ['checkout-order-summary--loading', isLoading],
        className,
      ])}
    >
      <Heading
        level={2}
        className="checkout-order-summary__title"
        children={<Text id="Checkout.OrderSummary.title" />}
      />

      <Divider variant="primary" />

      {isLoading && (
        <ProgressSpinner className="checkout-order-summary__spinner" />
      )}

      <div
        data-testid="order-summary-subtotal"
        className="checkout-order-summary__row"
      >
        <p>
          <Text id="Checkout.OrderSummary.subtotal" />
        </p>
        <Price
          amount={subtotal.amount}
          currency={subtotal.currency}
          className={classes(['checkout-order-summary__price', className])}
        />
      </div>

      {subtotal.isTaxIncl && (
        <div
          data-testid="order-summary-subtotal-incl-tax"
          className="checkout-order-summary__row checkout-order-summary__row--muted"
        >
          <p>
            <Text id="Checkout.OrderSummary.includingTaxes" />
          </p>
        </div>
      )}

      {shipping != undefined && (
        <div
          data-testid="order-summary-shipping"
          className="checkout-order-summary__row"
        >
          <p data-testid="order-summary-shipping-label">
            {shipping.isEstimated ? (
              <Text id="Checkout.OrderSummary.estimatedShipping" />
            ) : (
              <Text id="Checkout.OrderSummary.shipping" />
            )}
          </p>

          {shipping.amount === 0 ? (
            <p
              data-testid="estimated-shipping"
              className="checkout-order-summary__price"
            >
              <Text id="Checkout.OrderSummary.freeShipping" />
            </p>
          ) : (
            <Price
              data-testid="estimated-shipping"
              amount={shipping.amount}
              currency={shipping.currency}
              className={classes(['checkout-order-summary__price', className])}
            />
          )}
        </div>
      )}

      {tax != undefined && !tax.breakdown && (
        <div
          data-testid="order-summary-tax"
          className="checkout-order-summary__row"
        >
          <p>
            <Text id="Checkout.OrderSummary.tax" />
          </p>
          <Price
            amount={tax.amount}
            currency={tax.currency}
            className={classes(['checkout-order-summary__price', className])}
          />
        </div>
      )}

      {tax != undefined && tax.breakdown && (
        <>
          <Divider variant="secondary" />

          <div className="checkout-order-summary__row checkout-order-summary__row--margin-bottom">
            <p>
              <Text id="Checkout.OrderSummary.taxBreakdown" />
            </p>
          </div>

          {tax.breakdown.map((tax) => (
            <div
              data-testid="order-summary-tax-breakdown"
              key={tax.label}
              className="checkout-order-summary__row checkout-order-summary__row--muted"
            >
              <p>{tax.label}</p>

              <Price
                amount={tax.amount.value!}
                currency={tax.amount.currency!}
                className={classes([
                  'checkout-order-summary__price',
                  className,
                ])}
              />
            </div>
          ))}

          <div
            data-testid="order-summary-tax-breakdown-total"
            className="checkout-order-summary__row checkout-order-summary__row--strong checkout-order-summary__row--margin-top"
          >
            <p>
              <Text id="Checkout.OrderSummary.taxTotal" />
            </p>

            <Price
              amount={tax.amount}
              currency={tax.currency}
              className={classes(['checkout-order-summary__price', className])}
            />
          </div>

          <Divider variant="secondary" />
        </>
      )}

      <div
        data-testid="order-summary-total-incl-tax"
        className="checkout-order-summary__row checkout-order-summary__row--emphasized"
      >
        <p>
          <Text id="Checkout.OrderSummary.total" />
        </p>

        <Price
          amount={total.inclTax.amount}
          currency={total.inclTax.currency}
          className={classes(['checkout-order-summary__price', className])}
        />
      </div>

      {total.exclTax != undefined && (
        <div
          data-testid="order-summary-total-excl-tax"
          className="checkout-order-summary__row checkout-order-summary__row--muted"
        >
          <p>
            <Text id="Checkout.OrderSummary.totalExclTax" />
          </p>

          <Price
            amount={total.exclTax.amount}
            currency={total.exclTax.currency}
            className={classes(['checkout-order-summary__price', className])}
          />
        </div>
      )}
    </Card>
  );
};
